body{
  background:#F9FAF7;
}
/*HEADER Styles */
.header{
  max-width: 90%;
}
.logo{
  font-family: 'EB Garamond', serif;
  font-size: 3rem;
  color: #f97552;
  width: 25%;
  text-align: center;
}

.header > *, .navigation > *  {
  display: inline-block;
  
}
.navigation{
  border: 1px solid #ddd;
  border-radius: 30px;
  width: 48%;
}
.navigation > * {
  text-transform: uppercase;
  font-family: 'Fira Sans Condensed', sans-serif;
  text-align: center;
}
.link {
  text-decoration: none;
  color: rgb(62, 62, 126);
  position: relative;

}
.link:hover{
  color:#f97552;
}
.link:hover::after{
  content:'';
  position:absolute;
  width: 100%;
  left:0;
  top:40px;
  border-bottom: 2px solid #f97552;  
}
.navigation h4{
  width: 25%;
}

.login{
  width: 25%;
  text-align: right;
  text-transform: uppercase;
  font-family: 'Fira Sans Condensed', sans-serif;
  color: rgb(62, 62, 126);
}
.fa-user-circle{
  margin-left: 5%;
  padding: 8px 8px;
  color:#f97552;
  border:1px solid #f97552;
  border-radius: 50%;
  cursor: pointer;
}

/*Section1 Styles */
.section1{
  display: flex;
}
.flex1, .flex2{
  flex:1;
}
.flex h2{
  font-family: 'EB Garamond', serif;
  color: rgb(62, 62, 126);
}
.subtitle1{
  text-align: right;
  margin-left:17%;
  font-size: 3.5rem;
}
.flex p{
  font-family: 'Fira Sans Condensed', sans-serif;
  width:55%;
  position: relative;
}
.section1-par{
  margin-left: 40%;
}
.section1-par, .section2-par, .down-container-left p{
  color: rgb(62, 62, 126);
  font-size: 1.3rem;
}
.flex button{
  margin: 10% 33%;
}
.fa-angle-right{
  margin-left: 10px;
  color:#f97552;

}
.fa-chart-bar{
  position: absolute;
  color:rgb(145, 142, 142);
  top:0;
  left:-18%;
}

/*Section2 Styles */
.section2{
  display: flex;
  margin-top: 10%;
}
.text-container{
  flex-basis:80%;
}
.subtitle2{
  margin: 20% 0 30% 25%;
  font-size: 3.3rem;
}
.title{
  text-transform: uppercase;
  color:#f97552;
  letter-spacing: 12px;
  font-size: 0.8rem;
}
.text-container p, .text-container button{
  margin-left: 25%
}

.img-container{
  display:flex;
  flex-wrap:wrap;
  justify-content: flex-start;
  align-items: baseline;
}
.img-container img{
  width: 42%;
}
hr{
  height: 3px;
  border:none;
  background:#f97552;
}
.text-container hr{
  width: 10%;
  margin-left:25%;
}
/*Section3 Styles */
.section3{
  display: flex;
  flex-wrap: wrap;
  margin-top: 10%;
  justify-content: flex-start;
}
.subtitle3{
  margin: 5% 0% 5% 10%;
  font-size: 3.3rem;
  width: 30%;
}
.subtitle3, span{
  display: inline-block;
}
.upper-container .title{
  margin-left:10%;
}
.upper-container{
  flex-basis:100%
}
.panorama-container {
  display: flex;
  justify-content: center;
}
.panorama-container img{
  width: 85%;
  height: 90%;
}
span{
  font-size: 9rem;
  font-family: 'EB Garamond', serif;
  color:rgb(145, 142, 142);
  position: relative;
  margin-left: 30%;
}
sup{
  font-size: 3rem;
  position: absolute;
  top: 35%;
}

.down-container{
  flex:1;
  display: flex;
  justify-content: space-between
}
.down-container-left p{
  width: 55%;
  margin:4% 0 0 15%;
}
.down-container-left button{
  margin: 4% 15%;
}

.down-container-right{
  width: 50%;
  display: flex;
  justify-content: space-evenly;
  margin-top:2%;
}
.first-par{
  color:#f97552;
  text-transform: uppercase;
  font-size: .7rem;
  font-weight: bold;
}
.second-par{
  color: rgb(62, 62, 126);
  width: 70%;
  padding: 10px 0 20px 0;
  font-family: 'Fira Sans Condensed', sans-serif;
  font-weight: 500;
}
.second-par + hr{
  width: 50px;
  margin-left: 0%;
}
.slogan-container > img{
  max-width: 100%;
}

/* Section 4 Styles */

.section4{
  display: grid;
  grid-template-areas: "first second third";
  margin-bottom: 10%;
  max-width: 90%;
  justify-content: space-around;
}
.first{
 grid-area:first;
 margin-left: 25%;
 color: rgb(62, 62, 126);
}
.subtitle4{
  font-family: 'EB Garamond', serif;
  font-size: 3rem;
  width: 57%;
}
.first hr{
  width: 10%;
  margin-left: 0%;
}
.first h5{
  font-size: 1.3rem;
  width: 55%;
}

.first input{
  width: 85%;
  border: 0;
  border-bottom: 1px solid #ddd;
  padding: 10px;
  margin: 25px 0;
  background:#F9FAF7;

}
.first input:focus{
  outline: none;
}
.first .title{
  margin-top: 25%;
}
.first > button{
  margin-top: 10%;
}
.second{
  grid-area:second;
  background: #fff;
  color: rgb(62, 62, 126);
  box-shadow: 
  15px 5px 0px 0px #f1f1f1, 
  20px 10px 0px 0px #eee, 
  25px 15px 15px 5px #ddd;
 }
 .number{
   color: #bbb;
   padding: 5px 50px;
   margin-top: 18%;
 }
 .second h4{
  font-size: 2rem;
  font-family: 'EB Garamond', serif;
  padding: 10px 40px;
  width: 50%;
 }
 .second hr{
  width: 10%;
  margin-left: 10%;
  margin-bottom: 40px;
  margin-top: 30px;
}
.second a{
  text-transform: uppercase;
  color:#f97552;
  text-decoration: none;
  font-size: .8rem;
  margin-left: 10%;
  margin-bottom: 40px;
  
}
 .text{
   width: 80%;
   margin: 20px;
   padding: 5px 20px;
 }
 .date{
  color:#f97552;
  padding: 5px 40px;
 }
 .third{
  grid-area:third;
 }
 .arrow{
    color: #eee;
    border: 1px solid #eee;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    text-align: center;
    line-height: 40px;
    vertical-align: middle;
    padding: 30px;
    margin: 20px 55px;
    cursor: pointer;
 }
.arrow:first-child{
  margin-top: 100px;
}
 .arrow:hover{
   background: #f97552;
   color: #fff;
 }

 .third > button{
    margin-top: 200px;
    margin-left: 55px;
    cursor: pointer;
 }

/* Footer*/
.footer{
  display:grid;
  grid-template-columns:39% 13% 13% 35% ;
  grid-template-areas: "copy gen press links";
  max-width: 100%;
  justify-content: space-evenly;
  background: rgb(55, 78, 55);
  color: #bbb;
  align-items: center;
  height: 120px;
}
.footer > div > p {
  font-size: .8rem;
}
.copy{
  grid-area:copy;
  text-align: center;
}
.email-gen{
  grid-area:gen;

}
.email-press{
  grid-area:press;

}
.links{
  grid-area:links;
  display: flex;
  justify-content: center;
  align-items: center;
}
.links > .link2 {
text-decoration: none;
color: #bbb;
margin:30px;
padding-bottom: 5px;
border-bottom: 1px solid #bbb;
font-size: .8rem;
}
